import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import ExplicadorIndex from '@/components/profiles/Index'
import Login from '@/components/auth/Login'
import Register from '@/components/auth/Register'

Vue.use(Router)

export default new Router({
  routes: [
      {
          path: '/',
          name: 'Home',
          component: Home
      },
      {
          path: '/:profile/profile',
          name: 'Index',
          component: ExplicadorIndex
      },
      {
          path: '/login',
          name: 'Login',
          component: Login
      },
      {
          path: '/register',
          name: 'Register',
          component: Register
      }
  ],
    mode: 'history'
})



