// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import Vuex from 'vuex'
import { store } from "./store/index";

// Import dos componentes Vue
import AddDialog from './components/dialogs/AddDialog.vue'
import Explicador from './components/menus/MainMenuRight.vue'
import MenuLeft from './components/menus/MainMenuLeft.vue'
import AuthDropdown from './components/dropdowns/AuthDropdown.vue'

Vue.component('add-dialog',AddDialog)
Vue.component('explicador',Explicador)
Vue.component('menu-left',MenuLeft)
Vue.component('auth-dropdown',AuthDropdown)

Vue.use(Vuex)
Vue.use(Vuetify, { theme: {
  primary: '#ee44aa',
  secondary: '#424242',
  accent: '#82B1FF',
  error: '#FF5252',
  info: '#2196F3',
  success: '#4CAF50',
  warning: '#FFC107'
}})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router, store,
  components: { App },
  template: '<App/>',
  created() {
    //  Ocultando o menu principal, para mostrar as opcoes do menu do perfil
    var isProfilePage = this.$route.path.toString().indexOf('profile') > 0
    if (isProfilePage) { this.$store.dispatch('togleMenu',false) }

  }
})



