<template>
    <div class="text-xs-center">
        <v-menu offset-y xs3>
            <!--<v-btn color="primary" dark slot="activator">Dropdown</v-btn>-->
            <v-btn icon large title="Voce nao esta autenticado" slot="activator">
                <v-avatar size="32px" >
                    <img
                            src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"
                            alt=""
                    >
                </v-avatar>
            </v-btn>
            <v-list>
                <v-list-tile v-for="item in dropdownItems" :key="item.title" @click="" router :to="item.link">
                    <v-list-tile-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile>
            </v-list>
        </v-menu>
    </div>
</template>
<script>
    export default {
        data: () => ({
        }),
        computed: {
            dropdownItems () {
                return this.$store.getters.getDropdownItems
            }
        }
    }
</script>


