export default {
    state: {
        loading: false,
        error: null,
        main_menu: [
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: true, icon: 'settings', text: 'Configuracoes' },
            { aditional: true, icon: 'add', text: 'Adicionar nova ' },
        ],
        profile_main_menu: [

            { aditional: false, icon: 'help', text: 'Dados Pessoais' },
            { aditional: false, icon: 'help', text: 'Dados academicos' },
            { aditional: false, icon: 'help', text: 'Localizacao' },

        ],
        profile_menu_right: [
            { aditional: false, icon: 'help', text: 'Disciplinas que explica' },
            { aditional: false, icon: 'help', text: 'O que dizem sobre ele' },
            { aditional: false, icon: 'help', text: 'Sua disponibilidade' },
            { aditional: false, icon: 'help', text: 'Seus Estudantes' },
        ],
        home: true,
        tipos: [
            'Disciplinas','Cursos','Classes','Escolas','Universidades','Institutos'
        ],
        auth_dropdown: [
            {icon: 'help', title: 'Login', link: '/login' },
            {icon: 'help', title: 'Registo ', link: '/register' },
            {icon: 'help', title: 'Perfil', link: '/perfil/profile' },
            {icon: 'help', title: 'Log out', link: '/' }
        ]
    },
    mutations: {
        togleMenu (state,value) {
            state.home = value
        }
    },
    getters: {
        getMenuOptions (state) {
            return state.home ? state.main_menu : state.profile_main_menu
        },
        getMenuRightSide (state) {
            return state.profile_menu_right
        },
        isHomePage (state) {
            return state.home
        },
        getDropdownItems (state) {
            return state.auth_dropdown
        },
        getTipos (state) {
            return state.tipos
        }
    },
    actions: {
        togleMenu ({commit}, value) {
            commit('togleMenu',value)
        }
    },
}


